/* ProductImage.css */

.product-details-image {
  display: flex;
  /* justify-content: center; */
  /* align-items: center; */
  height: 100%;
}

.product-details-image img {
  max-width: 100%;
  max-height: 100%; 
  object-fit: contain;
}

@media (max-width: 767px) {
  .product-details-image{
    height: 40vh;
    width: 100%;
  }
  .product-details-image img{
    height: 40vh;
    width: 100%;
    object-position: top;
    object-fit: cover;
  }
}
@media (min-width: 768px) {
  .product-details-image  {
    width:  70%;
    padding: 2em;
  }
  .product-details-image img {
    width: 100%;
    max-height: 500px;
  }
}
